/** @jsx jsx */
import { jsx } from '@ali/4ever-dev-test';
import {
  Value,
  Controller,
} from '@ali/4ever-cangjie';

const defaultValue = ((
  <value>
    <document>
      <block type="paragraph">
        Hello <cursor />
        Cangjie
      </block>
    </document>
  </value>
) as unknown) as Value;

describe('Bi/contextMenu/behaviour', () => {
  test('implement `contextMenu` hook', () => {
    const contextMenu = jest.fn();
    const controller = Controller.create({
      value: defaultValue,
      plugins: [
        {
          contextMenu,
        },
      ],
    });
    controller.run('contextMenu');
    expect(contextMenu).toBeCalledTimes(1);
  });
});
